import React from 'react'
import Link from 'gatsby-link'
import get from 'lodash/get'
import Helmet from 'react-helmet'


// import Lightbox from 'react-images'
import Gallery from "../components/Gallery"

import thumb01 from '../assets/images/thumbs_ML/carpeta_n3.jpg'
import thumb02 from '../assets/images/thumbs_ML/carpeta_n3-2.jpg'

import full01 from '../assets/images/fulls/carpeta_n3.jpg'
import full02 from '../assets/images/fulls/carpeta_n3-2.jpg'

const DEFAULT_IMAGES = [
    { id: '1', src: full01, thumbnail: thumb01, caption: 'Photo 1', description: 'Set De Jardín De Infantes Personalizado En Quilmes Cole Kit'},
    { id: '2', src: full02, thumbnail: thumb02, caption: 'Photo 2', description: 'Lorem ipsum dolor sit amet nisl sed nullam feugiat.'}
];

class HomeIndex extends React.Component {

    

    render() {
        //const siteTitle = this.props.data.site.siteMetadata.title
        //const siteDescription = this.props.data.site.siteMetadata.description

        return (
            <div>
                <Helmet>
                        <title>hi</title>
                        <meta name="description" />
                </Helmet>

                <div id="main">

                    <section id="one">
                        <header>
                            <h2>Carpetas N° 3 (2 Tapas, Forrada En Tela)</h2>
                            <h4>$84</h4> 
                        </header>
                        <p>DESCRIPCIÓN:<br />
                        Carpetas N° 3 (2 tapas, forrada en tela).<br />
                        Se pueden personalizar con cualquier imagen o foto.</p>
                       
                       <a mp-mode="dftl" href="https://www.mercadopago.com/mla/checkout/start?pref_id=8068205-fb7ced94-a50e-4fbc-821a-9fdc16e62db8" name="MP-payButton" className='blue-ar-l-rn-none preview__button'>Pagar</a><br /><br />
                        <Link to="/">Volver</Link>
                         <br /><br />

                    </section>
                
                        <h2></h2>
                    
                        <Gallery images={DEFAULT_IMAGES.map(({ id, src, thumbnail, caption, description }) => ({
                            src,
                            thumbnail
                        }))} />

                        
                    

                </div>

            </div>
        )
    }
}

export default HomeIndex


